import React from 'react';
import '../Styles/Layout.css'
import logo from '../assests/LOGO.png'
import { Link, NavLink, useNavigate} from 'react-router-dom';
import { Container, Row, Button, Col} from 'reactstrap';
import leaf_img from '../assests/images/leaf.png';
import manali_video from '../assests/images/manali_video.mp4'
import kedarnath_video from '../assests/images/kedarnath_video.mp4'
import Nepal_video from '../assests/images/Nepal_video.mp4';
import ServiceCard from './../Services/ServiceCard';
import weather from '../assests/images/weather.png'
import guide from '../assests/images/guide.png'
import coustimize from '../assests/images/coustimize.png';
import FeaturedTourList from './FeaturedTourList';
import experience from '../assests/images/experience.jpg';
import MasonaryImagesGallery from './MasonaryImagesGallery';
import Testimonial from './Testimonial';
import Newsslatter from '../shared/Newsslatter';
import Footer from './Footer';
import { useSelector } from 'react-redux';
import { message } from 'antd';

const serviceData =[
  {
  imgUrl: weather,
  title: "Calculated Weather",
  desc: 'sunshine paints landscapes, rain whispers tales, winds carry secrets—natures enchanting symphony unfolds.'
  },
  {
    imgUrl: guide,
    title: "Tour Guide",
    desc: ' A storyteller of history, architect of memories, unlocking the worlds wonders with passion.'
    },
    {
      imgUrl: coustimize,
      title: "Customization",
      desc: 'Crafting dream journeys, tailoring adventures to desires, turning wanderlust into personalized, cherished experiences.'
      },
  
]


const userMenu = [
  { 
  path: '/', 
  display: 'Home' 
},
  { 
  path: '/about', 
  display: 'About'
 },
  { 
  path: '/tours', 
  display: 'Tour'
},
];

const adminMenu = [
  {
    path: '/admin-home',
    display: "Home",
  },
  {
    path: '/bookings',
    display: "Bookings",
  },
]

const superMenu = [
{
  path: '/', 
  display: "Home" 
},
{ 
  path: '/users', 
  display: "User" 
},
{ 
  path: '/admins', 
  display: "Admin" 
},
];

const handleLogout = () => {
  localStorage.clear();
  message.success("Logout Successfully");
  setTimeout(() => {
    window.location.href = '/login';
  }, 2000);
}


const Layout = () => {
  const { user } = useSelector(state => state.user);
  const navigate = useNavigate();

  let dynamicNavLinks;
  if (user?.isAdmin) {
    dynamicNavLinks = adminMenu;
  } else if (user?.isSuper) {
    dynamicNavLinks = superMenu;
  } else {
    dynamicNavLinks = userMenu;
  }



  let homePagePath;
  if (user?.isAdmin) {
    homePagePath = '/admin-home'; //admin
  } else if (user?.isSuper) {
    homePagePath = '/superadmin-home'; //super admin
  } else {
    homePagePath = '/'; //users
  }



  React.useEffect(() => {
    navigate(homePagePath, );
  }, [homePagePath, navigate]);


  return (
    <header className='header'>
      <Container>
        <Row>
          <div className='nav__wrapper d-flex align-items-center justify-content-between'>
            {/* logo start here */}
            <div className='logo'>
              <img src={logo} alt='' />
            </div>
            {/* logo end here */}

            {/* menu start here */}
            {/* menu start here */}
              <div className='navigation'>
              <ul className='menu d-flex align-items-center gap-5'>
                {dynamicNavLinks.map((item, index) => (
                  <li className='nav__item' key={index}>
                    <NavLink to={item.path} className={navClass => navClass.isActive ? 'active__link' : ''}>{item.display}</NavLink>
                  </li>
                ))}
              </ul>
            </div>
            {/* menu end here */}
            <div className='right'>
            <div className='nav__btns' onClick={handleLogout}>
            <Button className='btn secondary__btn'><Link to='/login'>Logout</Link></Button>
                </div>
            <div className='nav__right'>
            <Link to=''>{user?.name}</Link>
              
            </div>
            </div>
          </div>
        </Row>
      </Container>
      {/* ============= image section ================ */}
      <section>
        <Container>
          <Row>
            <Col lg='6'>
              <div className='hero_content'>
                <div className='hero__subtitle d-flex align-items-center'>
                  <h3 className='highlights'>KNOW BEFORE YOU GO</h3>
                  <img src={leaf_img} alt='' />
                </div>
                <h1>Travelling opens the door to creating 
                <span className='highlights'> memories</span></h1>
                <p>Traveling is the art of discovering the poetry woven into the fabric of the world, where each step is a stanza, and every horizon whispers a verse of adventure, inviting our hearts to dance with the rhythm of exploration.</p>
              </div>
            </Col>
            <Col lg= '2'>
            <div className='hero__img-box'>
              <video src={manali_video} alt='' autoPlay loop muted />
              </div>
            </Col>
            <Col lg= '2'>
              <div className='hero__img-box mt-4'>
              <video src={kedarnath_video} alt='' autoPlay loop muted />
              </div>
            </Col>
            <Col lg= '2'>
              <div className='hero__img-box mt-5'>
              <video src={Nepal_video} alt='' autoPlay loop muted />
              </div>
            </Col>
          </Row>
        </Container>
      </section>
      {/* ============= image section ================ */}

      {/* ================ service section start here ======== */}
      <section>
        <Container>
          <Row>
            <Col lg='3'>
              <h5 className='services_subtitle'>What we serve</h5>
              <h2 className='services_title'>We offer our best service</h2>
            </Col>
            {serviceData.map((item, index) => (
              <Col lg='3' key={index}>
                <ServiceCard item={item} />
                </Col>
            ))}
          </Row>
        </Container>
      </section>
      {/* ================ service section end here ======== */}

      {/* ================ featured section start here ======== */}
      <section>
        <Container>
          <Row>
            <Col lg='12' className='mb-5'>
              <div className='featured_tour-title'>
                <h1>Our Feature</h1>
                <h2>Best place to visit this season</h2>
              </div>
            </Col>
            <FeaturedTourList />
          </Row>
        </Container>
      </section>
      {/* ================ featured section end here ======== */}


      {/* ================ experience section start here ======== */}
      <section>
        <Container>
          <Row>
            <Col lg='6'>
              <div className='experience_content'>
                <h1>Experience </h1>
                <h2>With our all experience <br /> we will serve you</h2>
                <p>
                Embark on unforgettable journeys with us! As your trusted tour and travel companion, we promise to craft the perfect experience for you. From picturesque destinations to seamless itineraries, our commitment is to provide you with the best, ensuring each trip is a cherished memory. Let the adventure begin!
                </p>
              </div>
              <div className='counter_wrapper d-flex align-items-center gap-5'>
                <div className='counter_box'>
                  <span>2 </span>
                  <h6>Successful Trips</h6>
                </div>
                <div className='counter_box'>
                  <span>10 +</span>
                  <h6>Regualr clients</h6>
                </div>
                <div className='counter_box'>
                  <span>1</span>
                  <h6>Year experience</h6>
                </div>
              </div>
            </Col>
            <Col lg='6'>
              <div className='experience_img'>
                <img src={experience} alt='' />
              </div>
            </Col>
          </Row>
        </Container>
      </section>
      {/* ================ experience  section end here ======== */}
      {/* ================ gallery  section start here ======== */}
      <section>
        <Container>
          <Row>
            <Col lg='12'>
            <div className='gallery_content'>
              <h1>Gallery</h1>
            </div>
            <h2 className='gallery_content2'>
              Visit our customers tour gallery
            </h2>
            </Col>
            <Col lg='12'>
              <MasonaryImagesGallery />
            </Col>
          </Row>
        </Container>
      </section>
      {/* ================ gallery  section end here ======== */}



      {/* ================ Testimonial  section start here ======== */}
      <section>
        <Container>
          <Row>
            <Col lg='12'>
              <h1 className='testimonial_title'>From user</h1>
            <h2 className='testimonial_content'>
              What our client say about us 
            </h2>
            </Col>
            <Col lg='12'>
              <Testimonial />
            </Col>
          </Row>
        </Container>
      </section>
      {/* ================ Testimonioal section end here ======== */}
      <Newsslatter />
            <Footer />
    </header>
    
  )
}

export default Layout;
