import React from 'react';
import '../Styles/Newsletter.css'
import Img from '../assests/images/newsletter-img.png';
import {Container, Row, Col,Button} from 'reactstrap'

const Newsslatter = () => {
  return (
    <section className='newsletter'>
    <Container>
        <Row>
            <Col lg='6'>
                <div className='newsletter_content'>
                    <h2>Subscribe now to get useful traveling information</h2>
                    <div className='newsletter_input'>
                    <input type='email' placeholder='Enter email here' required />
                        <Button className='btnm newsletter_btnm'>Subscribe</Button>
                    </div>
                    <p>
                        Subscribe for exclusive updates! Join us at to stay informed and enjoy special offers. Don't miss out!</p>
                </div>
            </Col>
            <Col lg='6'>
                <div className='newsletter_img'>
                    <img src={Img} alt='' />
                </div>
            </Col>
        </Row>
    </Container>
    </section>
  )
}

export default Newsslatter