import React from 'react'
import { Col, Container, ListGroup, ListGroupItem, Row } from 'reactstrap'
import logo from '../assests/LOGO.png'
import '../Styles/Footer.css'
import { Link } from 'react-router-dom'


const quic_links=[
    {
      path: "/",
      display: "Home"
    },
    {
      path: "/about",
      display: "About"
    },
    {
      path: "/tours",
      display: "Tours"
    },
  ]
  
  
  const quic_links2=[
    {
      path: "/gallery",
      display: "Gallery"
    },
    {
      path: "/register",
      display: "Register"
    },
    {
      path: "/login",
      display: "login"
    },
  ]

const Footer = () => {
    const year = new Date().getFullYear()
  return (
    <footer className='footer'>
    <Container>
        <Row>
        <Col lg='3'>
                <div className='logo'>
                    <img src={logo} />
                    <p>Embark on unforgettable journeys with us! Explore exotic destinations,craft enduring memories,let adventure be your guide !!</p>
                </div>
                <div className='social_links d-flex align-items-center gap-3'>
                    <span><Link to='#'><i class='bx bxl-instagram-alt'></i></Link></span>
                    <span><Link to='#'><i class='bx bxl-youtube'></i></Link></span>
                    <span><Link to='#'><i class='bx bxl-facebook-circle' ></i></Link></span>
                    <span><Link to='#'><i class='bx bxl-twitter' ></i></Link></span>
                </div>
            </Col>
            <Col lg='3'>
                <h5 className='footer_link-title'>Discover</h5>
                <ListGroup className='footer_quick-links'>
                    {
                        quic_links.map((item,index)=> (
                            <ListGroupItem key={index} className='ps-0 border-0'>
                                <Link to={item.path}>{item.display}</Link>
                            </ListGroupItem>
                        ))
                    }
                </ListGroup>
            </Col>
            <Col lg='3'>
            <h5 className='footer_link-title'>Discover</h5>
                <ListGroup className='footer_quick-links'>
                    {
                        quic_links2.map((item,index)=> (
                            <ListGroupItem key={index} className='ps-0 border-0'>
                                <Link to={item.path}>{item.display}</Link>
                            </ListGroupItem>
                        ))
                    }
                </ListGroup>
            </Col>
            <Col lg= '3'>
            <h5 className='footer_link-title'>Contacts Us</h5>
                <ListGroup className='footer_quick-links'>
                    <ListGroupItem className='ps-0 border-0 d-flex align-items-center gap-3'>

                              <h6 className='mb-0 d-flex align-items-center gap-2'>
                                <span><i class='bx bxs-map'></i></span>
                                Address :
                            </h6>
                        <p className='mb-0'>Howrah,West Bangal</p> 
                    </ListGroupItem>

                    <ListGroupItem className='ps-0 border-0 d-flex align-items-center gap-3'>
                        <h6 className='mb-0 d-flex align-items-center gap-2'>
                            <span><i class='bx bxs-envelope' ></i></span>
                            Email :
                            </h6>
                            <p className='mb-0'>reeturajvats587@gmail.com</p> 
                    </ListGroupItem>
                    <ListGroupItem className='ps-0 border-0 d-flex align-items-center gap-3'>
                        <h6 className='mb-0 d-flex align-items-center gap-2'>
                            <span><i class='bx bxs-phone' ></i></span>
                            Phone :
                            </h6>
                            <p className='mb-0'>6299045761</p> 
                    </ListGroupItem>     
                </ListGroup>
            </Col>
            <Col lg='12' className='text-center pt-5'>
                <p className='copyright'>
                    &copy;{year} Design and Develop by Reeturaj Kumar.
                </p>
            </Col>
        </Row>
    </Container>
    </footer>
  )
}

export default Footer