import React from 'react'
import { Link,useNavigate } from 'react-router-dom';
import { Form, Input, message } from 'antd';
import {useDispatch} from 'react-redux';
import {showLoading,hideLoading} from "../redux/feature/alertSlice"
import axios from 'axios'
import '../Styles/Register.css'

const Registerpage = () => {

  const navigate = useNavigate()
  const dispatch = useDispatch();

  const onFinishHandler = async(values) => {
    try {
      dispatch(showLoading())
      const res = await axios.post('http://localhost:8080/api/v1/user/register', values,)
      dispatch(hideLoading())
      if(res.data.success){
        message.success("Register Successfully !")
        navigate('/login')
      }else {
        message.error(res.data.message)
      }
    } catch (error) {
      dispatch(hideLoading())
      console.log(error)
      message.error("Something Went Wrong")
    }
  };


  return (
    <>
    <div className='form-container'>
    <div className='wrapper'>
      <span className='bg-animate'></span>

      <div className='form-box register'>
        <h1>CREATE ACCOUNT</h1>
        <Form onFinish={onFinishHandler}>
        <div className='input-box'>
            <Form.Item name='name' rules={[{ required: true, message: 'Please enter your Username' }]}>
              <Input type='text' placeholder='Enter name'/>
            </Form.Item>
            <i class='bx bxs-user'></i>
          </div>
          <div className='input-box'>
            <Form.Item name='email' rules={[{ required: true, message: 'Please enter your email' },
            {
              type: 'email',
              message: 'Please enter a valid email address',
            },
            {
                validator: (_, value) => {
                if (value && !value.endsWith('@gmail.com')) {
                return Promise.reject('');
            }
            return Promise.resolve();
          },
        },
      ]}>
        <Input type='email' placeholder='Enter email'/>
        </Form.Item>
        <i className='bx bxs-envelope'></i>
        </div>
          <div className='input-box'>
            <Form.Item name='password' rules={[{ required: true, message: 'Please enter your password' }]}>
              <Input type='password' placeholder='Enter password'/>
            </Form.Item>
            <i className='bx bxs-lock-alt'></i>
          </div>
          <button type='submit' className='btn'>
            Register
          </button>
          <div className='logreg-link'>
            <p>Already have an account? <Link to='/login'>Sign in</Link></p>
          </div>
        </Form>
      </div>
      <div className='info-text register'>
        <h1>Hey Buddy 🙋</h1>
        <p>Embark on unforgettable journeys with us! Explore exotic <br></br> destinations,craft enduring memories,let adventure be <br></br> your guide !!</p>
      </div>
    </div>
    </div>
    </>
  );
};

export default Registerpage