import React, {useRef, useState} from 'react';
import '../Styles/tour-details.css';
import { Container, Row, Button, Col,Form, ListGroup} from 'reactstrap';
import {useParams} from 'react-router-dom';
import tourData from '../assests/data/tour';
import TourLayout from '../Components/TourLayout';
import Footer from '../Components/Footer';
import calculateAvgRating from './../utils/avgRating';
import avatar from '../assests/images/av-logo.png'
import Booking from './../Components/Bookingpage';
import Newsslatter from './../shared/Newsslatter';

const TourDetails = () => {

  const {id} = useParams()
  const reviewMsgRef = useRef('')
  const [tourRating, setTourRating] = useState(null)
  const tour = tourData.find(tour=> tour.id === id)


  const {photo,title, desc, price,address, reviews, city, 
        maxGroupSize, desc2,desc4,desc5,desc6,desc7,desc8,
        desc9,desc10,desc11,desc12,desc13,desc14,make, make2, 
        make3, make4, make5, make6, make7, make8, make9, make10,
        make11, make12, make31,make32,make33, make34, make35, make36, 
        make37, make38, make39, make310, make311, make312, make313, make314,
        desc31,desc32, desc33, desc34, desc35, desc36, desc37, desc38, desc39
      , desc310, desc311, desc312, desc313, desc314 , make41, make42,make43,make44,
      make45,make46, make47, make48, desc41,desc42,desc43,desc44,desc45, desc46,
      desc47, desc48,
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    } = tour;


  const {totalRating,avgRating} = calculateAvgRating (reviews);
  //formate date

  const Options ={day: 'numeric', month: 'long', year: 'numeric'};

const submitHandler = e=> {
  e.preventDefault()
  const reviewText = reviewMsgRef.current.value;
  // alert(`${reviewText},${tourRating}`)
}


  return (
    <>
    <TourLayout />
    <section>
      <Container>
        <Row>
          <Col lg='8'>
            <div className='tour_content'>
              <img src={photo} alt='' />
              <div className='tour_info'>
                <h2>{title}</h2>
                
                <div className='d-flex align-items-center gap-5'>
                  <span className='tour_rating d-flex align-items-center gap-1'>
                    <i class='bx bx-star' style={{color : "#1097A3"}}></i>{avgRating === 0? null : avgRating}
                    {totalRating === 0 ? ('Not rated') :(<span>({reviews ?.length})</span>)}
                  </span>
                <span><i class='bx bxs-map'></i>  {address}</span>
              </div>
            <div className='tour_extra-details'>
              <span><i class='bx bx-map'></i> {city} </span>
              <span><i class='bx bx-rupee'></i> {price}/ per person</span>
              <span><i class='bx bxs-group' ></i> {maxGroupSize} People</span>
            </div>

            <div className='tour_extra-details2'><span>{desc}</span></div>
            <div className='near_vist'><h2>What makes this tour special</h2></div>

            <div className='near'>
            <span><i class='bx bxs-circle'></i>{make}{make31}{make41}</span><p>{desc2}{desc31}{desc41}</p>
            </div>

            <div className='near'>
            <span><i class='bx bxs-circle'></i>{make2}{make32}{make42}</span><p>{desc4}{desc32}{desc42}</p>
            </div>

            <div className='near'>
            <span><i class='bx bxs-circle'></i>{make3}{make33}{make43}</span><p>{desc5}{desc33}{desc43}</p>
            </div>

            <div className='near'>
            <span><i class='bx bxs-circle'></i>{make4}{make34}{make44}</span><p>{desc6}{desc34}{desc44}</p>
            </div>

            <div className='near'>
            <span><i class='bx bxs-circle'></i>{make5}{make35}{make45}</span><p>{desc7}{desc35}{desc45}</p>
            </div>

            <div className='near'>
            <span><i class='bx bxs-circle'></i>{make6}{make36}{make46}</span><p>{desc8}{desc36}{desc46}</p>
            </div>

            <div className='near'>
            <span><i class='bx bxs-circle'></i>{make7}{make37}{make47}</span><p>{desc9}{desc37}{desc47}</p>
            </div>

            <div className='near'>
            <span><i class='bx bxs-circle'></i>{make8}{make38}{make48}</span><p>{desc10}{desc38}{desc48}</p>
            </div>

            
            <div className='near_vist'>
            <h2>Things To Carry</h2>
            </div>

            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Most Important your ID proof</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Cap/Hats</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Sunscreen, moisturizer</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Glares</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Pair of clothes</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Towel</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Casual shoes and flip-flops</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Sanitary Kit</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Personal medicines (if any)An extra bag to keep your wet clothes</span>
            </div>

            <div className='near_vist'>
            <h2>Rules And Regulations</h2>
            </div>

            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Smoking and drinking  publicaly is  prohibited.</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Throwing garbage on trails is strictly not allowed.</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Participants should carry their own waste with them</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Personal belongings, baggage and luggage is completely personal responsibility and trekpanda is not responsible for loss of any baggage of personal belongings kindly take your own responsibility</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Playing loud music in the mountains is prohibited.</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Writing anything or any activity leading to disobedience of historical places is prohibited.</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>Team captains' decisions on any particular situation will be final for the mutual benefit of participants.</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>If the behavior of any trekker is likely to cause distress or harm to themselves, our member of staff or other team members, reserve the right to terminate their trip at any time and they will have to make their own arrangements; we will not be liable for any expenses incurred as a result. We will not entertain any claims arising due to such action.</span>
            </div>
            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>In case of any injuries/illness on a mountain/river/waterfall during a trip/activity, Trek Panda as a company or our local staff will not be responsible. We provide adequate human staff support in these areas to ensure the basic safety of the trekker. The trekker/customer has to bear all the expenses which may arise in case of medical exigency or emergency condition.</span>
            </div>

            <div className='rule_regulation'>
            <span><i class='bx bxs-circle'></i>As it is likely that in these sorts of trips weather must be taken into account if conditions are such that we have to change arrangements we will not be able to provide any sort of refund. If there are any other circumstances including but not limited to riot, political unrest, natural or other disasters, nuclear incident, terrorist activity, and we have to cancel a trip as a result of these circumstances, we will not be able to provide any sort of refund.</span>
            </div>


            </div>
            {/* review section start here */}
            <div className='tour_reviews mt-4'>
              <h4>Reviews({reviews?.length} reviews)</h4>
              <Form onSubmit={submitHandler}>
                <div className='d-flex align-items-center gap-3 mb-4 rating_group'>
                  <span onClick={() => setTourRating (1)}>1<i class='bx bxs-star' ></i></span>
                  <span onClick={() => setTourRating (2)}>2<i class='bx bxs-star' ></i></span>
                  <span onClick={() => setTourRating (3)}>3<i class='bx bxs-star' ></i></span>
                  <span onClick={() => setTourRating (4)}>4<i class='bx bxs-star' ></i></span>
                  <span onClick={() => setTourRating (5)}>5<i class='bx bxs-star' ></i></span>
                </div>
                <div className='review_input'>
                  <input type='text' ref={reviewMsgRef} placeholder='Share your thoughts' required />
                  <Button className='btnm text-white' type='submit'>Submit</Button>
                </div>
              </Form>
              <ListGroup className='user_reviews'>
                {
                  reviews?.map(review => (
                    <div className='review_item'>
                      <img src={avatar} alt='' />

                      <div className='w-100'>
                        <div className='d-flex align-items-center justify-content-between'>
                          <div>
                            <h6>Dhananjay Kumar</h6>
                            <p>
                              {new Date("01-12-2023").toLocaleDateString(
                                "en-US" , Options
                              )}
                            </p>
                          </div>
                          <span className='d-flex align-items-center'>
                            5<i class='bx bxs-star' ></i>
                          </span>
                        </div>
                        <h6>Good experience</h6>
                      </div>
                    </div>
                  ))
                }
              </ListGroup>
            </div>
            {/* review section end here */}
            </div>
          </Col>

          <Col lg="4">
            <Booking tour ={tour} avgRating = {avgRating}/>



            <div className='section'>
              <p>INCLUSIONS/EXCLUSIONS</p>
              <h2>What we'll give. What we won't</h2>
              <div className='what_wont'>
                <h6>What is included in the tour</h6>
                <span><i class='bx bxs-check-circle'></i>Stay in a hotel/Luxury Camps</span>
              </div>

              <div className='what_wont'>
                <span><i class='bx bxs-check-circle'></i>Local Guide</span>
              </div>
            </div>

            <div className='what_wont'>
                <span><i class='bx bxs-check-circle'></i>Guided Tours and Activities</span>
            </div>

            <div className='what_wont'>
                <span><i class='bx bxs-check-circle'></i>Breakfast, Lunch, Dinner</span>
            </div>

            <div className='what_wont'>
                <span><i class='bx bxs-check-circle'></i>Travel in a private vehicle for the duration of the trip.</span>
            </div>


            <div className='what_wont'>
                <span><i class='bx bxs-check-circle'></i>Crisis Management</span>
            </div>


            <div className='what_wont'>
                <span><i class='bx bxs-check-circle'></i>24/7 Customer Support</span>
            </div>




            <div className='what_wont2'>
            <h6>What is NOT included in the tour</h6>
            </div>

            <div className='what_wont2'>
                <span><i class='bx bxs-minus-circle'></i>Any kind of insurance</span>
            </div>

            <div className='what_wont2'>
                <span><i class='bx bxs-minus-circle'></i>Whatever isn't mentioned in the inclusion section</span>
            </div>

            <div className='what_wont2'>
                <span><i class='bx bxs-minus-circle'></i>Transaction fees differ based on payment method.</span>
            </div>
            <div className='what_wont2'>
                <span><i class='bx bxs-minus-circle'></i>Your camera and luggage are your responsibility </span>
            </div>
            <div className='what_wont2'>
                <span><i class='bx bxs-minus-circle'></i>Meals during the journey (train and Volvo stops)</span>
            </div>
            <div className='what_wont2'>
                <span><i class='bx bxs-minus-circle'></i>Excludes personal expenses, tours, insurance, laundry.</span>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
    <Newsslatter />
    <Footer />
    </>
  )
}

export default TourDetails