import React from 'react'
import { Link,useNavigate } from 'react-router-dom';
import { Form, Input, message } from 'antd';
import {useDispatch} from 'react-redux';
import {showLoading,hideLoading} from "../redux/feature/alertSlice"
import axios from 'axios'

const Loginpage = () => {


  const navigate = useNavigate()
  const dispatch = useDispatch()
    const onFinishHandler = async(values) => {
        try {
          dispatch(showLoading())
          const res = await axios.post('http://localhost:8080/api/v1/user/login',values)
          dispatch(hideLoading())
          if(res.data.success){
            localStorage.setItem("token",res.data.token)
            message.success('Login Successfully')
            navigate('/')
          }else{
            message.error(res.data.message)
          }
        } catch (error) {
          dispatch(hideLoading())
          console.log(error)
          message.error("Something Went Wrong")
        }
    };
  return (
    <>
    <div className='form-container'>
    <div className='wrapper'>
    <span className='bg-animate2'></span>

    <div className='form-box login'>
      <h1>LOGIN</h1>
      <Form onFinish={onFinishHandler}>
        <div className='input-box'>
          <Form.Item name='email' rules={[{ required: true, message: 'Please enter your email' },
          {
            type: 'email',
            message: 'Please enter a valid email address',
          },
          {
              validator: (_, value) => {
              if (value && !value.endsWith('@gmail.com')) {
              return Promise.reject('');
          }
          return Promise.resolve();
        },
      },
    ]}>
      <Input type='email' placeholder='Enter email'/>
      </Form.Item>
      <i className='bx bxs-envelope'></i>
      </div>
        <div className='input-box'>
          <Form.Item name='password' rules={[{ required: true, message: 'Please enter your password' }]}>
            <Input type='password' placeholder='Enter password'/>
          </Form.Item>
          <i className='bx bxs-lock-alt'></i>
        </div>
        <button type='submit' className='btn'>
          Login
        </button>
        <div className='logreg-link'>
          <p>Dont't have an account? <Link to='/register'>Sign up</Link></p>
        </div>
      </Form>
    </div>
    <div className='info-text login'>
    <h2>WELCOME BACK🤚</h2>
        <p>Thrilled to have you here! Get set<br></br>for more incredible journeys and<br></br> discover new horizons with us.<br></br>Let's explore together!</p>
    </div>
  </div>
  </div>
  </>
);
};

export default Loginpage