import React from 'react';
import { Container,Row,Col } from 'reactstrap';
import '../Styles/CommonSection.css'

const CommonSection = () => {
  return (
    <section className='common_section'>
        <Container>
            <Row>
                <Col lg ='12'>
                </Col>
            </Row>
        </Container>
    </section>
  )
}

export default CommonSection