import React from 'react';
import {Card, CardBody} from 'reactstrap';
import {Link} from 'react-router-dom'
import '../Styles/Layout.css'
import calculateAvgRating  from '../utils/avgRating';



const TourCard = ({tour}) => {

    const {id, title, city, photo, price, featured, reviews} = tour;

    const {totalRating,avgRating} =  calculateAvgRating(reviews)
  return <div className='tour_card'>
    <Card>
        <div className='tour_img'>
            <img src={photo} alt='tour-img' />
            {featured && <span></span>}
        </div>
    </Card>
    <CardBody>
        <div className='card_top d-flex align-items-center
        justify-content-between'>
            <span className='tour_location d-flex align-items-center gap-1'>
            <i class='bx bx-map'></i>{city}
            </span>
            <span className='tour_rating d-flex align-items-center gap-1'>
            <i class='bx bx-star' ></i>{avgRating === 0? null : avgRating}
            {totalRating === 0 ? ('Not rated') :(<span>({reviews.length})
            </span>)}
            </span>
        </div>
        <h5 className='tour_title'><Link to={`/tours/${id}`}>{title}</Link></h5>
                    
        <div className='card_bottom d-flex align-items-center justify-content-between mt-3'>
            {/* <div className='card_bottom d-flex align-items-center justify-content-between mt-3'>
            <h5>{price}</h5> */}
            <button className='btn booking_btn'>
                <Link to={`/tours/${id}`}>Book Now</Link>
            </button>
        </div>
    </CardBody>
  </div>
}

export default TourCard