import React,{useState} from 'react'
import '../Styles/Booking.css'
import { Button, Form, FormGroup, ListGroup, ListGroupItem }  from 'reactstrap'
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { hideLoading, showLoading } from '../redux/feature/alertSlice';
import axios from 'axios'
import { message } from 'antd';


const Bookingpage = ({tour , avgRating}) => {
    const {user} = useSelector(state => state.user)
    const {price, reviews} = tour
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [credentials, setCredentials] = useState({
        userId: '01',
        email: '',
        fullname: '',
        phone: '',
        guestSize: '',
        bookAt: '',
    })


    const handleChange = e => {
        setCredentials(prev=>({...prev, [e.target.id]:e.target.value}))
    };


    const serviceFee = 10
    const totalAmount = Number(price) * Number(credentials.guestSize) + Number(serviceFee)* Number(credentials.guestSize)


    const handleClick = async () => {
        try {
          dispatch(showLoading());
          const res = await axios.post(
            'http://localhost:8080/api/v1/booking',
            {
               tourName: tour.title, 
              fullName: credentials.fullname,
              phone: credentials.phone,
              email: credentials.email,
              guestSize: credentials.guestSize,
              bookAt: credentials.bookAt,
            },
            {
              headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`,
              },
            }
          );
          dispatch(hideLoading());
          if (res.data.success) {
            message.success(res.data.message);
            navigate('/thank-you')
          } else {
            message.error(res.data.message);
          }
        } catch (error) {
          dispatch(hideLoading());
          if (error.response) {
            message.error(`Server Error: ${error.response.data.message}`);
          } else if (error.request) {
            message.error('No response received from the server');
          } else {
            message.error(`Error: ${error.message}`);
          }
          console.error(error);
        }
      };
      



  return (
    <div className='booking'>
        <div className='booking_top d-flex align-items-center justify-content-between'>
            <h3><i class='bx bx-rupee'>{price} <span>per person</span></i></h3>
            <span className='tour_rating d-flex align-items-center'>
            <i class='bx bx-star'></i>{avgRating === 0? null : avgRating} 
            ({reviews?.length})
            </span>
        </div>
        {/* BOOING FORM START HERE */}
        <div className='booking_form' >
            <h5>Book Your Trip</h5>
            <Form className='booking_info-form' onSubmit={handleClick} >
                <FormGroup>
                    <input type='text'placeholder='Enter Full Name Here ' id='fullname' required onChange={handleChange} />
                </FormGroup>
                <FormGroup>
                    <input type='number'placeholder='Enter Full Phone Number Here' id='phone' required onChange={handleChange} />
                </FormGroup>
                <FormGroup>
                    <input type='email'placeholder='Enter Email Here ' id='email' required onChange={handleChange} />
                </FormGroup>
                <FormGroup className='d-flex align-items-center gap-3'>
                    <input type='date'placeholder='' id='bookAt' required onChange={handleChange} />
                    <input type='number'placeholder='Group Size' id='guestSize' required onChange={handleChange} />
                </FormGroup>

                <div className='booking_bottom'>
                    <ListGroup>
                        <ListGroupItem className='border-0 px-0'>
                            <h5 className='d-flex align-items-center gap-1'><i class='bx bx-rupee'></i>{price} <i class='bx bx-x' ></i> 1 person</h5>
                            <span><i class='bx bx-rupee'></i>{price}</span>
                        </ListGroupItem>

                        <ListGroupItem className='border-0 px-0'>
                            <h5>Service Charge</h5>
                            <span><i class='bx bx-rupee'></i>{serviceFee}</span>
                        </ListGroupItem>

                        <ListGroupItem className='border-0 px-0 total'>
                            <h5>Total Amount</h5>
                            <span><i class='bx bx-rupee'></i>{totalAmount}</span>
                        </ListGroupItem>


                        <ListGroupItem className='border-0 px-0'>
                            <Button className='btn text-white w-100 mt-4 ' onClick={handleClick}>Book Now</Button>
                        </ListGroupItem>
                    </ListGroup>
                </div>
            </Form>
        </div>
    </div>
  )
}

export default Bookingpage